import React, { useState, useEffect } from "react";
import { Button, Tabs, Tab, Box, Anchor, MaskedInput } from "grommet";
import CardListing from "../../components/CardList";
import { Cart } from "grommet-icons";
import CRUDForm from "../CRUDForm";
import ProductFormConfig from "./ProductFormConfig";
import Modal from "../../components/Modal";
import { crud, searchProduct } from "../../library/parse";
import EventObserver from "../EventObserver";
import SellingMode from "./SellingMode";
const Component = ({ storeId, editMode }) => {
  const [cudMode, setCUDMode] = useState("");
  const [activeModel, setActiveModel] = useState({});
  const [
    scheduledForFetchingProduct,
    setScheduledForFetchingProduct,
  ] = useState(false);
  const [
    scheduledForFetchingProductCart,
    setScheduledForFetchingProductCart,
  ] = useState(false);
  const [products, setProducts] = useState([]);

  const loadProductCarts = () => {};

  const loadProducts = () => {
    searchProduct({
      storeId: {
        f: "equalTo",
        v: storeId,
      },
      limit: 10,
      descending: "createdAt",
    }).then((e) => setProducts(e));
  };

  useEffect(() => {
    loadProducts();
  }, [storeId]);

  return (
    <Box>
      {editMode && (
        <Box fill={true} pad="small" direction="row" align="end">
          <Anchor onClick={() => setCUDMode("create")}>Add Product</Anchor>
        </Box>
      )}
      <CardListing
        dataList={products.map((e) => {
          return {
            title: e.get("name"),
            thumbnail: e.get("photo") && e.get("photo").url(),
            cta: (
              <>
                <SellingMode
                  product={e}
                  onAction={(action, model) => {
                    if (action === "edit") {
                    }
                  }}
                />
                {editMode && (
                  <Button
                    onClick={() => {
                      setActiveModel(e.toJSON());
                      setCUDMode("update");
                    }}
                    size="small"
                    margin="small"
                    primary
                    label="Edit"
                  />
                )}
              </>
            ),
          };
        })}
      />
      {cudMode && (
        <Modal onClose={() => setCUDMode("")}>
          <Tabs>
            <Tab title="Information">
              <CRUDForm
                onClick={(action, model) => {
                  crud(
                    "Product",
                    {
                      ...model,
                      available: true,
                    },
                    action,
                    "",
                    {
                      storeId: storeId,
                    }
                  );
                  setCUDMode("");
                }}
                mode={cudMode}
                model={activeModel}
                formConfig={ProductFormConfig}
                objectClass="Product"
              />
            </Tab>
            <Tab title={<Cart />}></Tab>
          </Tabs>
        </Modal>
      )}
      <EventObserver
        objectClass={"Product"}
        onEvent={(e, action) => {
          if (!scheduledForFetchingProduct) {
            setTimeout(() => {
              loadProducts();
              setScheduledForFetchingProduct(false);
            }, 5000);
            setScheduledForFetchingProduct(true);
          }
        }}
      />
      <EventObserver
        objectClass={"Product"}
        onEvent={(e, action) => {
          if (!scheduledForFetchingProduct) {
            setTimeout(() => {
              loadProducts();
              setScheduledForFetchingProductCart(false);
            }, 5000);
            setScheduledForFetchingProductCart(true);
          }
        }}
      />
    </Box>
  );
};

export default Component;
